<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process257"></a>

<p><br></p>
<p><span class="rvts17">大锤的陨落（</span><span class="rvts50">Hammerfall</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">主线任务</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:300,Y:480</span><span class="rvts21">，地点：卢西安大教堂祈祷神殿</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">进入大教堂隐藏的地下室</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1412.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">到达大教堂，通过一段鲜血之路后，会找到一处卢西安的神圣雕像，与它交谈。它会问你是否圣洁（询问你偷过东西没），无论你怎样选择答案，它都会立马处死你。</span>
<br><span class="rvts21">因此尽可能的不要与这个雕像对话，这明显是个坑。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1413.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">找到教堂旁边的一处隐藏按钮。由于整个卢西安大教堂的机关都是一个玩具商人参与制作的。</span>
<br><span class="rvts21">因此，如果此时队伍已经在</span><span class="rvts20"></span><span class="rvts21">一个工艺人的梦想和噩梦</span><span class="rvts20"></span><span class="rvts21">支线中从玩具商人那里获得了一个源力充能项链（需要充满源力，一共需要充</span><span class="rvts20">5</span><span class="rvts21">点源力），并且在该支线中的那个</span><span class="rvts20">NPC</span><span class="rvts21">玩具商人的商店上层获得了</span><span class="rvts20"></span><span class="rvts21">赎罪卷轴</span><span class="rvts20"></span><span class="rvts21">，那么会顺利打开血池中的地下室大门。其实并不是必须完成</span><span class="rvts20"></span><span class="rvts21">一个工艺人的梦想和噩梦</span><span class="rvts20"></span><span class="rvts21">支线，你完全可以杀掉玩具商人，获得项链，之后到商店楼上偷出</span><span class="rvts20"></span><span class="rvts21">赎罪卷轴</span><span class="rvts20"></span><span class="rvts21">（这样做，甚至连阿虎大人都不用救）。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1414.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">进入血池中的隐藏地下室。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1415.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">接下来在地下室会遇到一处类似于</span><span class="rvts20"></span><span class="rvts21">水管工</span><span class="rvts20"></span><span class="rvts21">的解密。只要把三路不同的能量传输到远处对应的能量池便可以激活石棺下的隐藏通道。</span>
<br><span class="rvts21">需要注意的是，如图，首先需要在操作台的一处下水道那里，献上鲜血，并在所有的管道结点连接成功后，祝福鲜血，才会使被祝福的鲜血涌向对面的能量池。</span>
<br><span class="rvts21">还有一种更简单的方法，可以不用做</span><span class="rvts20"></span><span class="rvts21">水管工</span><span class="rvts20"></span><span class="rvts21">的谜题。那就是使用</span><span class="rvts20"></span><span class="rvts21">传送金字塔</span><span class="rvts20"></span><span class="rvts21">，把角色传送到能量池中，使用各种法术技能激活能量槽（三个能量槽对应的是</span><span class="rvts20"></span><span class="rvts21">祝福火</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">祝福水</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">祝福血</span><span class="rvts20"></span><span class="rvts21">），这是比较</span><span class="rvts20">BUG</span><span class="rvts21">的一种方法。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1416.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">所有的管道结点连接如图所示。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1417.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">当</span><span class="rvts20">3</span><span class="rvts21">处能量池全部被激活后，楼梯旁的巨大石棺被移开，出现一处隐藏的地下室通道。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1418.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">在到达神殿的大门处，会出现一些队友间的对话。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1419.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">接着会到达一处叫做</span><span class="rvts20"></span><span class="rvts21">烦恼之路（</span><span class="rvts20">Line of Anguish</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">的区域。遭遇一些像是</span><span class="rvts20"></span><span class="rvts21">地精机械</span><span class="rvts20"></span><span class="rvts21">的怪物。他们会尝试拉下各种闸刀制造混乱的局面。</span>
<br><span class="rvts21">这些机械怪物一旦被杀掉会自动复活（无限复活）。因此需要在他们魔法护甲值低于</span><span class="rvts39">0</span><span class="rvts21">的时候，使用</span><span class="rvts20"></span><span class="rvts21">源力汲取</span><span class="rvts20"></span><span class="rvts21">技能，彻底杀掉它们。</span>
<br><span class="rvts21">根据英文</span><span class="rvts20"></span><span class="rvts21">电源</span><span class="rvts20"></span><span class="rvts21">的单词为</span><span class="rvts20">揚OWER</span><span class="rvts21">，取出所有的字母，找到对应的杠杆并按照顺序拉下（不按顺序会激活陷阱并最终无法成功打开能量墙），如图所示：</span>
<br><span class="rvts20">P-O-W-E-R</span><span class="rvts21">对应的是潜力（</span><span class="rvts20">Potency</span><span class="rvts21">），秩序（</span><span class="rvts20">Order</span><span class="rvts21">），智慧（</span><span class="rvts20">Wisdom</span><span class="rvts21">），热情（</span><span class="rvts20">Empathy</span><span class="rvts21">），正直（</span><span class="rvts20">Righteousness</span><span class="rvts21">）。</span>
<br><span class="rvts21">但是由于汉化内容会略有差异（机翻汉化差异最大），因此笔者直接给出它们的位置。</span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">潜力（</span><span class="rvts20">Potency</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">杠杆位置。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1420.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">秩序（</span><span class="rvts20">Order</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">杠杆位置。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1421.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">智慧（</span><span class="rvts20">Wisdom</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">杠杆位置。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1422.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">热情（</span><span class="rvts20">Empathy</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">杠杆位置。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1423.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">正直（</span><span class="rvts20">Righteousness</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">杠杆位置。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1424.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">到达下一层楼梯处，一段寒暄后，将进入《神界：原罪</span><span class="rvts20">2</span><span class="rvts21">》的大结局主线</span><span class="rvts20"></span><span class="rvts21">终结时刻（</span><span class="rvts20">End Times</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



